"use client"

import type React from "react"

import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog"
import { Button } from "@/components/ui/button"
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form"
import { Input } from "@/components/ui/input"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Separator } from "@/components/ui/separator"
import { Switch } from "@/components/ui/switch"
import { Textarea } from "@/components/ui/textarea"
import { zodResolver } from "@hookform/resolvers/zod"
import type { Category, Product, Vendor } from "@prisma/client"
import { type Control, useFieldArray, useForm } from "react-hook-form"
import * as z from "zod"
import { Carousel, CarouselContent, CarouselItem, CarouselNext, CarouselPrevious } from "@/components/ui/carousel"
import { AspectRatio } from "@/components/ui/aspect-ratio"
import { X } from "lucide-react"
import { useState } from "react"
import { productService } from "@/services/api-service"
import { MultiSelect } from "./multi-select"
import { useToast } from "@/components/ui/use-toast"
import { Skeleton } from "@/components/ui/skeleton"

const MAX_IMAGE_SIZE = 5 * 1024 * 1024 // 5MB
const ACCEPTED_IMAGE_TYPES = ["image/jpeg", "image/jpg", "image/png", "image/webp"]

const imageSchema = z
  .any()
  .refine((files) => {
    if (!files) return true
    let valid = true
    if (Array.isArray(files)) {
      files.forEach((file) => {
        if (file?.size > MAX_IMAGE_SIZE) {
          valid = false
        }
        if (!ACCEPTED_IMAGE_TYPES.includes(file?.type)) {
          valid = false
        }
      })
    } else {
      if (files?.size > MAX_IMAGE_SIZE) {
        valid = false
      }
      if (!ACCEPTED_IMAGE_TYPES.includes(files?.type)) {
        valid = false
      }
    }
    return valid
  }, "Max image size is 5MB and accepted types are .jpeg, .jpg, .png, .webp")
  .optional()

const variantOptionSchema = z.object({
  name: z.string().min(2, {
    message: "Variant option name must be at least 2 characters.",
  }),
  values: z.string().array().optional(),
})

const productFormSchema = z.object({
  name: z.string().min(2, {
    message: "Product name must be at least 2 characters.",
  }),
  description: z.string().optional(),
  price: z.number().min(0, {
    message: "Price must be greater than 0.",
  }),
  compareAtPrice: z.number().optional(),
  costPerItem: z.number().optional(),
  vendor: z.string().min(1, {
    message: "Vendor must be selected.",
  }),
  categories: z.string().array().optional(),
  images: z.any().optional(),
  variantOptions: z.array(variantOptionSchema).optional(),
  primaryImageIndex: z.number().optional(),
  inventory: z
    .object({
      trackInventory: z.boolean().optional(),
      quantity: z.number().optional(),
      allowBackorders: z.boolean().optional(),
      lowStockThreshold: z.number().optional(),
    })
    .optional(),
  shipping: z
    .object({
      weight: z.number().optional(),
      weightUnit: z.enum(["kg", "g", "lb", "oz"]).optional(),
      dimensions: z
        .object({
          length: z.number().optional(),
          width: z.number().optional(),
          height: z.number().optional(),
          unit: z.enum(["cm", "in"]).optional(),
        })
        .optional(),
    })
    .optional(),
})

export type ProductFormValues = z.infer<typeof productFormSchema>

interface ProductFormProps {
  vendors: Vendor[]
  categories: Category[]
  initialData?: Product & {
    vendor: Vendor
    categories: Category[]
  }
  setIsAddOpen: (value: boolean) => void
}

export function ProductForm({ vendors, categories, initialData, setIsAddOpen }: ProductFormProps) {
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [imageFiles, setImageFiles] = useState<File[]>([])
  const [activeImageIndex, setActiveImageIndex] = useState<number | null>(0)
  const [deletedImageIds, setDeletedImageIds] = useState<number[]>([])

  const form = useForm<ProductFormValues>({
    resolver: zodResolver(productFormSchema),
    defaultValues: initialData
      ? {
          name: initialData?.name || "",
          description: initialData?.description || "",
          price: initialData?.price || 0,
          compareAtPrice: initialData?.compareAtPrice || 0,
          costPerItem: initialData?.costPerItem || 0,
          vendor: initialData?.vendor.id || "",
          categories: initialData?.categories.map((category) => category.id) || [],
          variantOptions: (initialData?.variantOptions as any) || [],
          primaryImageIndex: initialData?.primaryImageIndex || 0,
          inventory: {
            trackInventory: initialData?.inventory?.trackInventory ?? true,
            quantity: initialData?.inventory?.quantity ?? 0,
            allowBackorders: initialData?.inventory?.allowBackorders ?? false,
            lowStockThreshold: initialData?.inventory?.lowStockThreshold ?? 5,
          },
          shipping: {
            weight: initialData.shipping?.weight ?? undefined,
            weightUnit: initialData.shipping?.weightUnit || "kg",
            dimensions: {
              length: initialData.shipping?.dimensions?.length ?? undefined,
              width: initialData.shipping?.dimensions?.width ?? undefined,
              height: initialData.shipping?.dimensions?.height ?? undefined,
              unit: initialData.shipping?.dimensions?.unit || "cm",
            },
          },
        }
      : {
          name: "",
          description: "",
          price: 0,
          compareAtPrice: 0,
          costPerItem: 0,
          vendor: "",
          categories: [],
          variantOptions: [],
          primaryImageIndex: 0,
          inventory: {
            trackInventory: true,
            quantity: 0,
            allowBackorders: false,
            lowStockThreshold: 5,
          },
          shipping: {
            weight: undefined,
            weightUnit: "kg",
            dimensions: {
              length: undefined,
              width: undefined,
              height: undefined,
              unit: "cm",
            },
          },
        },
  })

  const { toast } = useToast()

  const { fields, append, remove } = useFieldArray({
    control: form.control,
    name: "variantOptions",
  })

  async function onSubmit(data: ProductFormValues) {
    console.log("the process is here now")
    setIsSubmitting(true)
    try {
      const formData = {
        ...data,
        vendor: data.vendor.toString(),
        categories: data.categories,
        variantOptions:
          data.variantOptions?.map((option) => ({
            ...option,
            values: option.values.filter((value) => value.trim() !== ""),
          })) || [],
        primaryImageIndex: activeImageIndex !== null ? activeImageIndex : undefined,
        inventory: {
          trackInventory: data.inventory?.trackInventory ?? true,
          quantity: data.inventory?.quantity ?? 0,
          allowBackorders: data.inventory?.allowBackorders ?? false,
          lowStockThreshold: data.inventory?.lowStockThreshold ?? 5,
        },
        // Preserve existing images if no new images are uploaded
        existingImages: initialData?.images || [],
      }

      let response

      console.log("Initial data:", initialData)
      console.log("Initial data ID:", initialData?.id)
      if (initialData?.id) {
        console.log("Updating existing product with ID:", initialData.id)
        response = await productService.updateProduct(initialData.id, formData, imageFiles, deletedImageIds)
      } else {
        console.log("Creating new product")
        response = await productService.createProduct(formData, imageFiles)
      }

      if (!response) {
        throw new Error("No response from server")
      }

      toast({
        title: "Success",
        description: initialData?.id ? "Product updated successfully" : "Product created successfully",
      })

      setIsAddOpen(response)
    } catch (error: any) {
      console.error("Error submitting product:", error)

      const errorMessage = error.message || "Failed to save product. Please try again."

      toast({
        title: "Error",
        description: errorMessage,
        variant: "destructive",
      })
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(e.target.files || [])
    setImageFiles(files)
    setActiveImageIndex(0)
  }

  const handleRemoveImage = (index: number) => {
    setImageFiles((prevImages) => {
      const newImages = [...prevImages]
      newImages.splice(index, 1)
      return newImages
    })
    setActiveImageIndex(null)
  }

  const handleRemoveExistingImage = (imageId: number) => {
    setDeletedImageIds((prevDeletedImageIds) => [...prevDeletedImageIds, imageId])
  }

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
        <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
          <FormField
            control={form.control}
            name="name"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Name</FormLabel>
                <FormControl>
                  <Input placeholder="Product name" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="description"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Description</FormLabel>
                <FormControl>
                  <Textarea placeholder="Product description" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>
        <Separator />
        <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
          <FormField
            control={form.control}
            name="price"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Price</FormLabel>
                <FormControl>
                  <Input type="number" step="0.01" placeholder="Product price" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="compareAtPrice"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Compare at price</FormLabel>
                <FormControl>
                  <Input type="number" step="0.01" placeholder="Compare at price" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="costPerItem"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Cost per item</FormLabel>
                <FormControl>
                  <Input type="number" step="0.01" placeholder="Cost per item" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="vendor"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Vendor</FormLabel>
                <Select onValueChange={field.onChange} defaultValue={field.value}>
                  <FormControl>
                    <SelectTrigger>
                      <SelectValue placeholder="Select a vendor" />
                    </SelectTrigger>
                  </FormControl>
                  <SelectContent>
                    {vendors.map((vendor) => (
                      <SelectItem key={vendor.id} value={vendor.id}>
                        {vendor.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="categories"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Categories</FormLabel>
                <FormControl>
                  <MultiSelect
                    options={categories.map((category) => ({
                      label: category.name,
                      value: category.id,
                    }))}
                    onChange={field.onChange}
                    value={field.value}
                  />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>
        <Separator />
        <div>
          <FormField
            control={form.control}
            name="images"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Images</FormLabel>
                <FormControl>
                  <Input
                    type="file"
                    multiple
                    accept="image/png, image/jpeg, image/jpg, image/webp"
                    onChange={handleImageChange}
                  />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />

          {(initialData?.images?.length || imageFiles.length > 0) && (
            <Carousel className="w-full max-w-md">
              <CarouselContent>
                {initialData?.images?.map((image, index) => (
                  <CarouselItem key={image.id} className="pl-1 md:basis-1/2 lg:basis-1/3">
                    <div className="p-1">
                      <AspectRatio ratio={1 / 1}>
                        <img
                          src={image.url || "/placeholder.svg"}
                          alt="Image"
                          className="object-cover rounded-md"
                          onClick={() => setActiveImageIndex(index)}
                        />
                      </AspectRatio>
                      <Button
                        variant="destructive"
                        size="sm"
                        className="absolute top-2 right-2"
                        onClick={() => handleRemoveExistingImage(image.id)}
                      >
                        <X className="h-4 w-4" />
                      </Button>
                    </div>
                  </CarouselItem>
                ))}
                {imageFiles.map((file, index) => (
                  <CarouselItem key={index} className="pl-1 md:basis-1/2 lg:basis-1/3">
                    <div className="p-1">
                      <AspectRatio ratio={1 / 1}>
                        <img
                          src={URL.createObjectURL(file) || "/placeholder.svg"}
                          alt="Image"
                          className="object-cover rounded-md"
                          onClick={() => setActiveImageIndex(index)}
                        />
                      </AspectRatio>
                      <Button
                        variant="destructive"
                        size="sm"
                        className="absolute top-2 right-2"
                        onClick={() => handleRemoveImage(index)}
                      >
                        <X className="h-4 w-4" />
                      </Button>
                    </div>
                  </CarouselItem>
                ))}
              </CarouselContent>
              <CarouselPrevious />
              <CarouselNext />
            </Carousel>
          )}
        </div>
        <Separator />
        <div>
          <div className="flex items-center justify-between">
            <h2>Variants</h2>
            <Button type="button" variant="outline" size="sm" onClick={() => append({ name: "", values: [] })}>
              Add variant
            </Button>
          </div>
          {fields.map((field, index) => (
            <div key={field.id} className="border rounded-md p-4 space-y-2">
              <div className="flex items-center justify-between">
                <h3>Variant {index + 1}</h3>
                <AlertDialog>
                  <AlertDialogTrigger asChild>
                    <Button type="button" variant="destructive" size="sm">
                      Remove
                    </Button>
                  </AlertDialogTrigger>
                  <AlertDialogContent>
                    <AlertDialogHeader>
                      <AlertDialogTitle>Are you absolutely sure?</AlertDialogTitle>
                      <AlertDialogDescription>
                        This action cannot be undone. This will permanently delete this variant.
                      </AlertDialogDescription>
                    </AlertDialogHeader>
                    <AlertDialogFooter>
                      <AlertDialogCancel>Cancel</AlertDialogCancel>
                      <AlertDialogAction onClick={() => remove(index)}>Continue</AlertDialogAction>
                    </AlertDialogFooter>
                  </AlertDialogContent>
                </AlertDialog>
              </div>
              <FormField
                control={form.control}
                name={`variantOptions.${index}.name`}
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Name</FormLabel>
                    <FormControl>
                      <Input placeholder="Variant name" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name={`variantOptions.${index}.values`}
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Values</FormLabel>
                    <FormControl>
                      <Input
                        placeholder="Variant values (comma separated)"
                        {...field}
                        onChange={(e) => {
                          const values = e.target.value.split(",")
                          field.onChange(values)
                        }}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>
          ))}
        </div>
        <Separator />
        <div>
          <h2>Inventory</h2>
          <div className="space-y-2">
            <FormField
              control={form.control}
              name="inventory.trackInventory"
              render={({ field }) => (
                <FormItem className="flex flex-row items-center justify-between rounded-lg border p-4">
                  <div className="space-y-0.5">
                    <FormLabel className="text-base">Track inventory</FormLabel>
                    <FormDescription>
                      When inventory tracking is enabled, the quantity of this product will be automatically updated
                      when orders are placed.
                    </FormDescription>
                  </div>
                  <FormControl>
                    <Switch checked={field.value} onCheckedChange={field.onChange} disabled={isSubmitting} />
                  </FormControl>
                </FormItem>
              )}
            />
            {form.watch("inventory.trackInventory") && (
              <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
                <FormField
                  control={form.control}
                  name="inventory.quantity"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Quantity</FormLabel>
                      <FormControl>
                        <Input type="number" placeholder="Quantity" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="inventory.lowStockThreshold"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Low stock threshold</FormLabel>
                      <FormControl>
                        <Input type="number" placeholder="Low stock threshold" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="inventory.allowBackorders"
                  render={({ field }) => (
                    <FormItem className="flex flex-row items-center rounded-lg border p-4">
                      <div className="space-y-0.5">
                        <FormLabel className="text-base">Allow backorders</FormLabel>
                        <FormDescription>
                          If enabled, customers will be able to place orders for this product even if it is out of
                          stock.
                        </FormDescription>
                      </div>
                      <FormControl>
                        <Switch checked={field.value} onCheckedChange={field.onChange} disabled={isSubmitting} />
                      </FormControl>
                    </FormItem>
                  )}
                />
              </div>
            )}
          </div>
        </div>
        <Separator />
        <div>
          <h2>Shipping</h2>
          <div className="space-y-2">
            <FormField
              control={form.control as Control<ProductFormValues>}
              name="shipping.weight"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Weight (optional)</FormLabel>
                  <FormControl>
                    <div className="flex gap-2">
                      <Input
                        type="number"
                        step="0.01"
                        min="0"
                        className="flex-1"
                        placeholder="Enter weight (optional)"
                        {...field}
                        value={field.value ?? ""}
                        onChange={(e) => {
                          const value = e.target.value === "" ? undefined : Number.parseFloat(e.target.value)
                          field.onChange(value)
                        }}
                      />
                      <Select
                        value={form.watch("shipping.weightUnit")}
                        onValueChange={(value) =>
                          form.setValue("shipping.weightUnit", value as "kg" | "g" | "lb" | "oz")
                        }
                      >
                        <SelectTrigger className="w-20">
                          <SelectValue placeholder="Unit" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="kg">kg</SelectItem>
                          <SelectItem value="g">g</SelectItem>
                          <SelectItem value="lb">lb</SelectItem>
                          <SelectItem value="oz">oz</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <div className="grid grid-cols-1 gap-4 md:grid-cols-3">
              <FormField
                control={form.control as Control<ProductFormValues>}
                name="shipping.dimensions.length"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Length (optional)</FormLabel>
                    <FormControl>
                      <Input
                        type="number"
                        step="0.01"
                        min="0"
                        placeholder="Length (optional)"
                        {...field}
                        value={field.value ?? ""}
                        onChange={(e) => {
                          const value = e.target.value === "" ? undefined : Number.parseFloat(e.target.value)
                          field.onChange(value)
                        }}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control as Control<ProductFormValues>}
                name="shipping.dimensions.width"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Width (optional)</FormLabel>
                    <FormControl>
                      <Input
                        type="number"
                        step="0.01"
                        min="0"
                        placeholder="Width (optional)"
                        {...field}
                        value={field.value ?? ""}
                        onChange={(e) => {
                          const value = e.target.value === "" ? undefined : Number.parseFloat(e.target.value)
                          field.onChange(value)
                        }}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control as Control<ProductFormValues>}
                name="shipping.dimensions.height"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Height (optional)</FormLabel>
                    <FormControl>
                      <Input
                        type="number"
                        step="0.01"
                        min="0"
                        placeholder="Height (optional)"
                        {...field}
                        value={field.value ?? ""}
                        onChange={(e) => {
                          const value = e.target.value === "" ? undefined : Number.parseFloat(e.target.value)
                          field.onChange(value)
                        }}
                      />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control as Control<ProductFormValues>}
                name="shipping.dimensions.unit"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Unit</FormLabel>
                    <FormControl>
                      <Select
                        value={form.watch("shipping.dimensions.unit")}
                        onValueChange={(value) => form.setValue("shipping.dimensions.unit", value as "cm" | "in")}
                      >
                        <SelectTrigger>
                          <SelectValue placeholder="Unit" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="cm">cm</SelectItem>
                          <SelectItem value="in">in</SelectItem>
                        </SelectContent>
                      </Select>
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>
          </div>
        </div>
        <Button type="submit" disabled={isSubmitting}>
          {isSubmitting ? <Skeleton width={100} /> : "Save"}
        </Button>
      </form>
    </Form>
  )
}
